
*{
  position: relative;
  margin: 0;
  padding: 0;
}
html,body{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.7);
}
#app {
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  /* -moz-osx-font-smoothing: grayscale; */
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */
}
.slide-fade{
  position: absolute;left:0;right: 0;
}
.slide-fade-enter-active {
  -webkit-transition: all .6s ease;
  transition: all .6s ease;
}
.slide-fade-leave-active {

  -webkit-transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);

  transition: all .1s cubic-bezier(2.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
{
  left:0;right: 0;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}

#index {
  position: absolute;
  width: 100%;
  min-height: 100%;
}
#index .loading {
    visibility: visible;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0, 0, 0, 0.7);
}
#index .loading p {
      color: #fff;
      text-align: center;
      margin-top: 50px;
}
#index .loading p.css11b4cd01ba1caf0 {
        background: -webkit-gradient(linear, left top, left bottom, from(#0871b9), to(#168f42));
        background: linear-gradient(to bottom, #0871b9, #168f42);
        -webkit-background-clip: text;
        color: transparent;
        font-size: 30px;
        margin-top: 200px;
}
#index .container {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-image: url(./static/img/index-bg.f4638dd.jpg);
    background-size: 100% auto;
    background-position: bottom;
    background-repeat: no-repeat;
    background-color: #29a6fe;
}
.index-head {
  width: 100%;
}
.btns {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  width: 90vw;
  margin-top: 3vw;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.btns a {
    display: block;
    width: 25vw;
    height: 25vw;
    text-align: center;
    margin-top: 3vw;
    margin-left: 7.5vw;
}
.btns a:nth-child(3n - 2) {
      margin-left: 0;
}
.btns a img {
      width: 12vw;
      height: 12vw;
}
.btns a span {
      display: block;
      margin-top: 10px;
      font-size: 14px;
}
@-webkit-keyframes loadinghide {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes loadinghide {
0% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.loading-hide {
  -webkit-animation: loadinghide 2s 1;
          animation: loadinghide 2s 1;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
@-webkit-keyframes containershow {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes containershow {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.container-show {
  -webkit-animation: containershow 2s 1;
          animation: containershow 2s 1;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
